<template>
  <v-container
    fluid
    grid-list-xl
  >
    <v-layout
      justify-center
      wrap
    >
      <v-flex
        md12
      >
        <material-card
          color="primary"
          flat
          full-width
          title="Notificações"
        >
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="items"
              slot-scope="{ item }"
            >
              <tr
                style="cursor: pointer"
                @click="openDialog(item)"
              >
                <td>
                  <core-notificacao-prioridade :prioridade="item.prioridade" />
                </td>
                <td>
                  <span :class="item.visualizado ? '' : 'nao-visualizado'">{{ item.titulo }}</span>
                </td>
                <td>
                  <v-chip
                    color="gray"
                    class="ml-0 body-2"
                    small
                  >
                    {{ item.tag }}
                  </v-chip>
                </td>
                <td>
                  <span :class="item.visualizado ? '' : 'nao-visualizado'">{{ formataData(item.dataCadastro) }}</span>
                </td>
                <td>
                  <v-icon
                    v-if="item.anexoId"
                    color="dark"
                    title="Baixar Anexo"
                    @click="downloadAnexo(item.anexoId)"
                  >mdi-paperclip</v-icon>
                </td>
              </tr>
            </template>
          </v-data-table>
        </material-card>
      </v-flex>
    </v-layout>
    <template
      v-if="dialog">
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="dialog"
          max-width="490">
          <v-card>
            <v-card-title class="title">
              {{ item.titulo }}
              <v-spacer />
              <v-icon @click="closeDialog">mdi-close</v-icon>
            </v-card-title>

            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex
                  md12
                  class="layout align-end caption"
                >
                  <span>{{ formataData(item.dataCadastro ) }}</span>
                  <v-spacer/>
                  <span>
                    Anexo
                  </span>
                  <v-icon
                    v-if="item.anexoId"
                    color="dark"
                    title="Baixar Anexo"
                    @click="downloadAnexo(item.anexoId)"
                  >mdi-paperclip</v-icon>
                </v-flex>
                <v-flex
                  md12
                  class="caption"
                >
                  <span
                  >
                    Notificação enviada por:
                  </span>
                  <v-icon
                    v-if="item.email"
                    color="dark"
                    small
                    title="Enviado Por E-mail"
                  >mdi-email</v-icon>
                  <v-icon
                    v-if="item.sms"
                    color="dark"
                    small
                    title="Enviado Por SMS"
                  >mdi-cellphone-message</v-icon>
                  <v-icon
                    v-if="item.whatsapp"
                    color="dark"
                    small
                    title="Enviado Por WhatsApp"
                  >mdi-whatsapp</v-icon>
                </v-flex>
                <v-flex md12>
                  <v-textarea
                    style="display: block;"
                    readonly
                    v-html="item.mensagem"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                color="primary"
                @click="closeDialog()">Ok</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <core-progress-modal
          :show="loading" />
      </v-layout>
    </template>
  </v-container>
</template>

<style scoped>
  .nao-visualizado {
    font-weight: 900 !important;
  }
</style>

<script>
import { NotificacaoBusiness, AnexoBusiness } from '../../business'
import EventBus from '../../utils/eventBus'

export default {
  metaInfo: {
    titleTemplate: '%s - Notificações'
  },
  data: () => ({
    loading: false,
    dialog: false,
    item: null,
    items: [],
    totalItems: 0,
    pagination: {
      pageNo: 1,
      rowsPerPage: 25,
      sortBy: 'dataCadastro'
    },
    filtros: {
      lojaId: null,
	  usuarioId: null,
      visualizado: null,
      prioridade: null,
      tag: ''
    },
    headers: [
      {
        sortable: false,
        text: 'Prioridade',
        value: 'prioridade'
      },
      {
        sortable: false,
        text: 'Titulo',
        value: 'titulo'
      },
      {
        sortable: false,
        text: 'Tag',
        value: 'tag'
      },
      {
        sortable: false,
        text: 'Data',
        value: 'dataCadastro'
      },
      {
        sortable: false,
        text: '',
        value: 'informacao'
      }
    ]
  }),
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  beforeMount () {
    let notificacaoId = this.$route.params.id
    if (notificacaoId) {
      this.loading = true
      NotificacaoBusiness.getById(notificacaoId)
        .then(response => {
          this.openDialog(response.data)
        }).finally(() => {
          this.loading = false
        })
    }
  },
  methods: {
    paginar () {
      this.loading = true
	  if(this.$root.isRolesPratico()){
		  this.filtros.usuarioId = this.$root.usuarioId()
		  NotificacaoBusiness.paginationPratico(this.filtros)
		.then(response => {
          this.items = response.data.content
          this.totalItems = response.data.totalElements
        })
        .finally(() => {
          this.loading = false
        })
	  }
	  else{
		  this.filtros.lojaId = this.$root.lojaId()
      	  NotificacaoBusiness.paginationLoja(this.filtros)
        .then(response => {
          this.items = response.data.content
          this.totalItems = response.data.totalElements
       	})
        .finally(() => {
    	  this.loading = false
        })
	  }
    },
    formataData (data) {
      if (data) {
        return this.$moment(data).calendar()
      }
    },
    openDialog (item) {
      this.item = item
      this.dialog = true
      if (!item.visualizado) {
        NotificacaoBusiness.marcarComoVisualizado(item.id)
          .then(() => {
            EventBus.$emit('NOTIFICACAO_VISUALIZADA', item.id)
            this.paginar()
          })
      }
    },
    closeDialog () {
      this.dialog = false
      this.item = null
    },
    downloadAnexo (anexoId) {
      AnexoBusiness.downloadAnexo(anexoId)
    }
  }
}
</script>
